.Home {
  background: linear-gradient(to bottom, #84B0DC, #D4EAF6);
  height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  padding-top: 180px;
  box-sizing: border-box;
}

.intro-bar {
  background: radial-gradient(#8AB0D8, #8AB0D8);
  padding: 10px 20px;
  border-radius: 50px;
  margin-bottom: 20px;
  width: 100%;
  max-width: 800px;
  text-align: center;
}

.intro-text {
  background: radial-gradient(#B5DFFF, #DCEAF4);
  -webkit-background-clip: text;
  background-clip: text;
  color: transparent;
  font-size: 24px;
  font-weight: bold;
}

.content-wrapper {
  background: rgba(255, 255, 255, 0.2);
  backdrop-filter: blur(10px);
  border-radius: 20px;
  padding: 20px;
  display: flex;
  flex-direction: column;
  align-items: center;
  max-width: 800px;
  width: 100%;
}

.content-top {
  display: flex;
  align-items: center;
  margin-bottom: 20px;
}

.content-logo {
  width: 111px;
  height: 111px;
  margin-right: 20px;
}

.content-description {
  font-size: 18px;
  color: white;
  text-align: left;
}

.content-bottom {
  display: flex;
  justify-content: center;
  width: 100%;
}

.buttons-preview-container {
  display: flex;
  align-items: center;
}

.buttons-background {
  background: rgba(255, 255, 255, 0.2);
  backdrop-filter: blur(10px);
  border-radius: 20px;
  padding: 10px;
  display: flex;
  align-items: center;
  justify-content: center;
}

.buttons {
  display: flex;
  flex-direction: column;
}

.option-button {
  background: rgba(4, 49, 68, 0.2);
  border: none;
  border-radius: 20px;
  padding: 10px;
  margin: 5px 0;
  color: white;
  width: 64px;
  height: 64px;
  cursor: pointer;
  transition: transform 0.2s;
  display: flex;
  align-items: center;
  justify-content: center;
}

.option-button.selected {
  background: rgba(4, 49, 68, 0.494);
  color: white;
}

.option-button:hover {
  background: rgba(4, 49, 68, 0.34);
  transform: scale(0.95);
}

.option-button:active {
  transform: scale(0.9);
}

.option-icon {
  width: 100%;
  height: 100%;
}

.preview {
  display: flex;
  flex-direction: column;
  align-items: center;
  text-align: center;
  flex: 1;
  max-width: 800px;
  margin-left: 10px;
}

.preview-container {
  position: relative;
  display: flex;
  align-items: flex-start;
  justify-content: center;
}

.preview-image {
  max-width: 100%;
  border-radius: 20px;
  filter: drop-shadow(0 0 10px rgba(0, 0, 0, 0.5));
  margin-top: 10px;
  mask-image: linear-gradient(to right, transparent, black 15%, black 85%, transparent);
}

.preview-overlay {
  position: absolute;
  top: 10px;
  left: 0;
  width: 100%;
  height: 20%;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: center;
  text-align: left;
  padding: 20px;
  box-sizing: border-box;
  background: rgba(56, 184, 244, 0.165);
  backdrop-filter: blur(10px);
  border-radius: 20px;
  z-index: 1;
  transition: opacity 0.5s ease;
}

.preview-title,
.preview-description {
  transition: opacity 0.5s ease;
}

.preview-container:hover .preview-overlay,
.preview-container:hover .preview-title,
.preview-container:hover .preview-description {
  opacity: 0;
}

.preview-title {
  font-size: 18px;
  color: white;
  margin: 0;
}

.preview-description {
  font-size: 14px;
  color: white;
  margin: 5px 0 0;
}
