.TopBar {
    display: flex;
    align-items: center;
    padding: 10px;
    backdrop-filter: blur(10px);
    position: fixed;
    width: 100%;
    top: 0;
    z-index: 1000;
  }
  
  .TopBar-logo {
    width: 111px;
    height: 111px;
    object-fit: contain;
    margin-right: 20px;
  }
  
  .TopBar nav {
    display: flex;
    margin-left: 10px;
  }
  
  .TopBar a {
    color: white;
    text-decoration: none;
    padding: 10px 20px;
    border-radius: 20px;
    transition: transform 0.2s;
    text-shadow: 0 4px 8px rgba(0, 0, 0, 0.2);
  }
  
  .TopBar a.active {
    color: white;
    background: radial-gradient(#8FB8DF, #C9F2FF);
    -webkit-background-clip: text;
    background-clip: text;
    text-shadow: none;
    position: relative;
  }
  
  .TopBar a.active::before {
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background: radial-gradient(#8FB8DF, #C9F2FF);
    border-radius: 20px;
    z-index: -1;
  }
  
  .TopBar a:hover {
    transform: scale(0.95);
  }
  
  .TopBar a:active {
    transform: scale(0.9);
  }
  
  ::-webkit-scrollbar {
    width: 12px;
  }
  
  ::-webkit-scrollbar-thumb {
    background: rgba(255, 255, 255, 0.3);
    border-radius: 10px;
    backdrop-filter: blur(10px);
  }
  