.footer {
    background: rgba(255, 255, 255, 0.2);
    backdrop-filter: blur(10px);
    border-radius: 20px;
    padding: 10px 20px;
    margin-top: 20px;
    width: 100%;
    max-width: 800px;
    text-align: center;
  }
  
  .footer-text {
    font-size: 14px;
    color: white;
    text-shadow: 2px 2px 3px rgba(0, 0, 0, 0.556);
  }
  